.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: .3s all;
  z-index: 999999;
}

.popup:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #282828 0%, #3e3e3e 100%);
  opacity: .9;
}

.popup.js-active {
  opacity: 1;
  visibility: visible;
}

.popup__close {
  position: absolute;
  top: 24px;
  right: 45px;
}

.popup__close svg {
  pointer-events: none;
}

