.header {
    min-height: 90px;
    padding: 10px 0;
    background-color: $grey;
    border-bottom: 1px solid $border-color;
    color: $primary-color;

    .container {
        display: flex;
        justify-content: space-between;
    }

    a {
        text-decoration: none;
        height: 100%;
        width: 100%;
        &:hover {
            background-color: rgba(#fff, .7);
        }
    }

    .profile {
        height: 100%;
        min-width: 190px;
        border: 1px solid;
        border-color: transparent $primary-color;

        span {
            font-size: 13px;
            margin-left: 10px;
        }

        a {
            padding: 0 20px;
            color: $primary-color;
        }
    }

    .logout {
        min-width: 57px;
        justify-content:center;
        border-right: 1px solid $primary-color;

        a {

            padding-left: 5px;
        }
    }
}
