html {
    font-family: 'OpenSans';
    font-weight: 500;
}
body {

    &.js-active {
        overflow: hidden;
    }
}
.out {
    min-width: 1370px;
}

.btn {
    position: relative;
    display: inline-block;
    border: 0;
    border-radius: 0;
    background-color: transparent;
    transition: .3s all;

    &-primary {
        background-color: $primary-color;
        color: #fff;
        font-size: 14px;
        font-weight: 300;
        line-height: 1;
        padding: 10px 20px;
        border: 2px solid $primary-color;
        min-width: 160px;

        &:hover,
        &.active {
            background-color: transparent;
            color: $primary-color;
        }
    }

    &-icon {
        min-width: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        width: 40px;
        height: 40px;
        svg {
            fill: #fff;
        }
        &:hover {
            svg {
                fill: $primary-color;
            }
        }
    }
}



.inner {
    padding-top: 70px;
}

.inner {
    padding-bottom: 100px;
}
.title-sm {
    font-size: 18px;
    font-weight: 600;
    color: $primary-color;
}

.inner__row {
    &:not(:first-child) {
        margin-top: 30px;
    }

    &_costs {

        .box__footer {
            margin-top: 40px;
        }
    }

    &_yield {
        margin-top: 70px !important;
    }
}
.sort {
    display: flex;
    align-items: center;
    font-size: 15px;

    &__list {
        margin-left: 10px;
    }

    .sort__item {

        &:not(:first-child) {
            border-top: 1px solid $primary-color;
            margin-top: 3px;
        }
        color: #b6bbc5;
        &.active {

            color: $primary-color;
            font-weight: 600;
        }
    }
}
.box {
    background-color: $grey;
    border: 1px solid $border-color;
    padding: 54px 32px 30px;

    &__header {
        padding-left: 20px;

        .box__select {
            padding-left: 40px;
            display: flex;
            align-items: center;

            .sort {
                margin-left: 30px;
            }
        }
    }

    .selectric-wrapper {
        display: inline-block;
        min-width: 175px;

        .selectric .label {
            margin-left: 25px;
        }
    }
    .title-sm {
        margin-bottom: 20px;
    }
}

.circle {
    text-align: center;

    .btn {
        margin-top: 50px;
        margin-left: -60px;
    }

    .title-sm {
        padding-left: 55px;
        text-align: left;
        margin-bottom: 55px;
    }

    &-canvas {
        margin-left: -60px;
        position: relative;

        &__text {
            position: absolute;
            top: 90px;
            left: 170px;
            font-weight: 300;
            font-size: 18px;
            color: $primary-color;
            span {
                display: block;
                font-size: 36px;
            }
        }

        &__list {
            margin-left: -85px;

            span {
                display: block;
                font-weight: 600;
            }

            .circle-canvas__item {
                color: #f86b4f;
                text-align: left;
                font-size: 15px;
                font-weight: 300;

                &:last-child {
                    margin-top: 10px;
                    color: #8bc4ea;
                }
            }
        }
    }

    &-custom {
        color: $primary-color;
        text-align: center;
        margin-left: 50px;

        .title-sm {
            text-align: left;
        }

        .circle {

            &-drow {
                margin: 65px 0 30px;
                width: 245px;
                height: 245px;
                border: 15px solid $primary-color;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                margin-left: 10px;

                .text {
                    font-weight: 300;
                    font-size: 18px;
                    span {
                        display: block;
                        font-size: 26px;
                        margin-bottom: 5px;
                    }
                }
            }


        }
    }
}
.box {

    #chart-finance,
    #chart-costs {
        width: 800px;
    }

    select {
        display: inline-block;
        width: auto;
    }
}

#doughnut-chart {
    width: 500px !important;
    height: 250px !important;
}

.title-15 {
    font-weight: 600;
    font-size: 15px;
    text-align: center;
}

.yield {

    .box__header {
        margin-bottom: 40px;

        .title-sm {
            margin-bottom: 40px;
        }
    }

    &__inner {
        .left {
            text-align: center;

            .btn {
                margin-top: 30px;
            }
        }
    }

    .wrapper {
        position: relative;
        padding: 0 80px;

        .tooltip-left,
        .tooltip-right {
            position: absolute;
            top: 30px;
            text-align: center;
            font-size: 15px;
            font-weight: 300;

            span {
                display: block;
                font-weight: 600;
                margin-top: 3px;
            }
        }

        .tooltip-center {
            position: absolute;
            font-size: 15px;
            font-weight: 300;
            text-align: center;
            bottom: 10px;
            left: 50%;
            margin-left: 5px;
            transform: translateX(-50%);
            color: $primary-color;

            span {
                display: block;
                font-weight: 600;
                margin-top: 3px;
            }
        }

        .tooltip-left {
            left: 0;
            color: #8bc4ea;
        }

        .tooltip-right {
            right: 0;
            color: #f86b4f;
        }
    }

    .right {

        padding-left: 60px;
    }


    &-slider {
        max-width: 520px;

        .slick-arrow {
            border: 1px solid #c9c9c9;
            border-radius: 0;
            opacity: 1;
            width: 39px;
            height: 39px;
            color: #c9c9c9;

            &.slick-prev {
                left: -40px;

                &:before {
                    content: '<';
                    color: inherit;
                    opacity: 1;
                    font-size: 14px;
                }
            }
            &.slick-next {
                right: -40px;

                &:before {
                    content: '>';
                    color: inherit;
                    opacity: 1;
                    font-size: 14px;
                }
            }

            &.slick-disabled {
                opacity: 0.5;
                cursor: default;

                &:before {
                    opacity: 1 !important;
                }

                &:hover {
                    border-color: #c9c9c9;
                    color: #c9c9c9 !important;
                }
            }

            &:hover {
                border-color: $primary-color;
                color: $primary-color;
            }
        }

        .title-15 {
            margin-bottom: 15px;
        }

        .slick-slide {
            margin: 0 10px;
            padding-bottom: 30px;
        }

        canvas {
            width: 140px !important;
            height: 70px !important;
            margin-left: auto;
            margin-right: auto;
        }

        .wrapper {
            padding: 0;

            .tooltip-center {
                bottom: -25px;
                margin-left: 0;

                a {
                    display: inline-block;
                    color: $primary-color;
                    margin-top: 15px;
                    font-size: 12px;
                    font-weight: 300;
                }
            }
        }
    }
}

.chart-legend > ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    user-select: none;
}

.chart-legend > ul > li {
    display: flex;
    cursor: pointer;
    align-items: center;
    margin: 0 15px 6px 0;
    color: #666;
    font-size: 15px;
}

.chart-legend > ul > li > span {
    display: block;
    width: 42px;
    height: 18px;
    margin-right: 5px;
}

.chart-legend > ul > li.hidden {
    text-decoration: line-through;
}

.chart-area-wrapper {
    width: 3000px;
}

.wrapper {
    position: relative;
}

.chart-wrapper {
    position: relative;
    width: 800px;
    margin-left: 50px;
    overflow-x: scroll;
}

#chart-axis {
    position: absolute;
    top: 0;
    left: 0;
    /* background: #F9F9F9; */
}

#chart-axis > ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    list-style: none;
    height: 100%;
    width: 50px;
    text-align: right;
    user-select: none;
    color: #11325b;
}


#chart-axis2 {
    position: absolute;
    top: 0;
    left: 0;
    /* background: #F9F9F9; */
}

#chart-axis2 > ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    list-style: none;
    height: 100%;
    width: 50px;
    text-align: right;
    user-select: none;
    color: #11325b;
}
