.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: .3s all;
    z-index: 999999;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to right, rgba(40, 40, 40, 1) 0%, rgba(62, 62, 62, 1) 100%);
        opacity: .9;
    }
    &.js-active {
        opacity: 1;
        visibility: visible;
    }

    &__close {
        position: absolute;
        top: 24px;
        right: 45px;

        svg {
            pointer-events: none;
        }
    }

    &__inner {
        background-color: #fff;
        padding: 40px 45px 40px 35px;
        position: relative;
        max-height: 90%;
        overflow: auto;
        min-width: 790px;
    }

    .head {
        margin-bottom: 45px;

        &__bottom {
            display: flex;
            margin-top: 30px;
        }

        .date-select {
            margin: 0 20px;
        }
    }

    .tabs {
        margin-top: 35px;
        display: flex;
        font-size: 15px;
        font-weight: 300;

        &__item {
            cursor: pointer;
            &:not(:first-child) {

                &:before {
                    content: '/';
                    display: inline-block;
                    margin: 0 6px;
                }
            }
            &.active {
                font-weight: 600;
            }
        }
    }

    .table {
        margin-top: 20px;
        max-width: 1300px;

        table {
            width: 100%;

            th, td {
                font-size: 15px;
                padding: 20px 15px;
                text-align: center;
                font-weight: 300;
            }

            td {
                white-space: nowrap;
            }


            thead {

                tr {
                    border-bottom: 2px solid $primary-color;
                }

                th {
                    font-weight: 600;
                    vertical-align: middle;
                }
            }

            tbody {

                tr {
                    font-weight: 300;

                    &:nth-child(odd) {
                        background-color: #f1f3f3;
                    }
                }
            }
        }
    }

    .title {
        font-size: 18px;
        font-weight: 600;
        color: $primary-color;
    }

    .box__footer {
        margin-top: 35px;
    }

    .group-buttons {
        margin-left: 90px;

        .btn {
            min-width: 10px;

            &:not(:first-child) {
                margin-left: 30px;
            }
        }
    }
}
#investments {

    .popup__inner {
        padding-bottom: 10px;
    }
}
.investments {

    &__head {
        margin-bottom: 20px;
        margin-left: -35px;
        margin-right: -45px;
        padding-left: 35px;
        padding-right: 45px;
        border-bottom: 1px solid #e3e3e3;
        padding-bottom: 13px;
        font-size: 13px;
        font-weight: 600;
        color: #273b5f;
    }

    &__footer {
        margin-bottom: 20px;
        margin-left: -35px;
        margin-right: -45px;
        padding-left: 100px;
        padding-right: 45px;
        border-top: 1px solid #e3e3e3;
        padding-top: 20px;
        font-size: 15px;
        color: #273b5f;

        span {
            &:not(:first-child) {
                margin-left: 10px;
            }
        }
    }

    &__body {
        max-height: 350px;
    }

    .col {
        width: 180px;
        text-align: center;
        padding: 0 10px;
    }

    .row {
        font-size: 13px;
        font-weight: 600;
        color: #273b5f;
        padding: 22px 0;
        border-bottom: 1px solid #e3e3e3;
    }
}


.date-select {
    height: 40px;
    border: 1px solid $primary-color;
    max-width: 300px;

    &-wrapper {
        height: 100%;
    }

    .field {
        position: relative;

        svg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 20px;
            fill: $primary-color;
            pointer-events: none;
        }
    }

    input {
        font-size: 14px;
        font-weight: 300;
        color: $primary-color;
        width: 50%;
        text-align: left;
        padding: 5px 22px;
        width: 100%;
        &:last-child {
            border-left: 1px solid #e3e3e3;
        }
    }
}


.accordion {
    margin-left: -35px;
    margin-right: -45px;
    border-bottom: 1px solid #e3e3e3;
    padding-left: 35px;
    padding-right: 45px;

    &__inner {
        max-height: 400px;

        .accordion__item {
            &:last-child {
                margin-bottom: 50px;
            }
        }
    }

    &__head {
        display: flex;
        align-items: center;
    }

    .row {

        width: 100%;
        padding: 20px 0;

        &__inner {
            display: flex;
            align-items: center;
        }

        &_child {

            &-wrapper {
                margin-top: 20px;
                display: none;
            }

            &-item {
                display: flex;
                align-items: center;
                width: 100%;
                font-size: 13px;

                .col {

                    &:first-child {
                        padding-left: calc( 45px + 27px);
                    }
                }

                &:not(:first-child) {
                    margin-top: 10px;
                }
            }
        }
    }

    .row_action {

        &:hover {
            background-color: #f2f9ef;
        }

        &.active {

        }

        .row__inner {
            .col {

                &:first-child {
                    &:after {
                        content: '';
                        display: inline-block;
                        width: 7px;
                        height: 5px;
                        background-image: url('../img/up.svg');
                        background-size: cover;
                        vertical-align: middle;
                        margin-left: 15px;
                        transform: rotate(180deg);
                    }
                }
            }
        }


    }

    &__item {

        &.active {

            .col_action{

                span {
                    &:after {
                        transform: rotate(0);
                    }
                }
            }
        }
    }

    &__body {
        display: none;
    }

    &__head {
        padding: 13px 0;
        border-bottom: 1px solid #e3e3e3;

        .col {

            &:first-child {
                padding-left: 10px;
            }
        }
    }
    &Child__item {
        border-bottom: 1px solid #e3e3e3;
        cursor: pointer;

        &:hover {
            background-color: #f1f3f3;

            .col {
                color: #273b5f;
            }
        }

        .col {
            font-size: 13px;
            color: #818181;

            &:first-child {
                padding-left: 45px;
            }
        }
    }
    .col {
        width: 50%;

        &_action {
            span {
                display: inline-block;
                padding: 10px 13px;
                font-size: 16px;
                font-weight: 600;
                background-color: #f1f3f3;
                position: relative;
                cursor: pointer;
                &:after {
                    content: '';
                    display: inline-block;
                    width: 7px;
                    height: 5px;
                    background-image: url('../img/up.svg');
                    background-size: cover;
                    vertical-align: middle;
                    margin-left: 15px;
                    transform: rotate(180deg);
                }
            }
        }
    }
}


.demo {
    padding-bottom: 50px;

    button {
        margin: 10px;
    }
}

#detailsProfit {

    .popup__inner {
        min-width: 1350px;
    }

    .accordion {
        text-align: center;
        &__inner {
            max-height: 350px;
        }
    }
    .accordionChild__item .col:first-child {
        padding-left: 0;
        span {
            padding-left: 40px;
        }
    }
}
