.inner__row_load {

    .box__footer {
        margin-top: 30px;
        text-align: center;
    }

    .selectric {
        background-color: #f9f9f9;

        .button {
            background-color: #f9f9f9;
        }
    }
}
.loadObject {
    margin-top: 23px;
    max-width: 800px;
    //border: 1px solid #d7d9d9;

    &__inner {
        display: flex;
        padding: 40px 30px 90px;
        border-bottom: 1px solid #d7d9d9;
    }

    .simplebar-content {
        border: 1px solid #d7d9d9;
        padding-bottom: 0 !important;
    }

    &__title {
        text-align: center;
        font-size: 18px;
        margin-bottom: 45px;
    }

    &__item {
        padding-right: 65px;

        &:last-child {
            padding-right: 30px;
        }
    }

    .column {
        width: 45px;
        position: relative;
        min-height: 230px;
        border: 1px solid #d7d9d9;
        border-radius: 8px;
        background-color: #f1f3f3;
        display: flex;
        align-items: flex-end;
        height: 100%;

        &__title-sm {
            position: absolute;
            top: 100%;
            left: 0;
            font-size: 12px;
            color: #273b5f;
            margin-top: 5px;

            strong {
                font-weight: 600;
            }
        }

        &:not(:first-child) {
            margin-left: 10px;
        }
        &:before {
            content: '';
            position: absolute;
            bottom: 100%;
            left: 50%;
            transform: translateX(-50%);
            height: 6px;
            width: 25px;
            border: 1px solid #d7d9d9;
            background-color: #b6bbc5;
            z-index: 1;
        }

        &__title {
            text-align: center;
            color: #b6bbc5;
            font-size: 18px;
            position: absolute;
            bottom: 100%;
            left: 0;
            width: 100%;
            margin-bottom: 5px;
        }

        &__body {
            width: 100%;
            position: relative;
            display: flex;
            flex-flow: column-reverse;
            justify-content:flex-start;
            align-items: center;
            padding-top: 4px;
            padding-bottom: 4px;
            padding-left: 1px;
            padding-right: 1px;


            .column__item {
                height: 20px;
                width: 100%;
                background-color: #80c966;
                margin-top: 2px;
                &:nth-child(1) {
                    background-color: #e95353;
                }
                &:nth-child(2) {
                    background-color: #e95353;
                }
                &:nth-child(3),
                &:nth-child(4) {
                    background-color: #e99a53;
                }
                &:nth-child(5){
                    background-color: #e99a53;
                }
                &:nth-child(6) {
                    background-color: #e9d253;
                }
                &:nth-child(7) {
                    background-color: #e9d253;
                }
                &:nth-child(1) {}
                &:nth-child(1) {}
            }
        }
    }
}
