@import "grid";

.container-fluid,
.container {
    width: 100%;
}

.container {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto;
}

.full-vp {
    height: 100vh;
}

.flex {
    display: flex;
}

.flex-wrap {
    display: flex;
    flex-flow: wrap;
}

.flex-vbottom {
    display: flex;
    align-items: flex-end;
}

.flex-vcenter {
    display: flex;
    align-items: center;
}

.flex-hcenter {
    display: flex;
    justify-content: center;
}

.flex-vhcenter {
    display: flex;
    justify-content: center;
    align-items: center;
}


