.attachments {
    margin-bottom: 60px;
    display: flex;

    &__left {
        display: flex;
        flex-flow: column;
        text-align: right;
        justify-content: space-between;
        font-size: 15px;
        color: $primary-color;
        margin-right: 10px;

        span {
            &:last-child {
                margin-bottom: 15px;
            }
        }
    }

    &__inner {
        min-width: 780px;
        border: 1px solid #d7d9d9;
        min-height: 340px;

        .columns__wrapper {
            margin-left: 30px;

            .column {
                &:not(:first-child) {
                    margin-left: 50px;
                }
            }
        }

        .left {
            display: flex;
            flex-flow: column;
            justify-content: space-between;
            height: 100%;
            padding: 13px;
            max-width: 75px;
            line-height: 1;

            span {
                font-size: 12px;
                color: #11325b;
                strong {
                    font-weight: 600;
                }
            }
        }
    }

    .column {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        flex-flow: column;
        align-items: center;
        min-width: 60px;
        position: relative;

        &__title {
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            margin-top: 5px;
            text-align: center;
            font-size: 15px;
            font-weight: 600;
            color: $primary-color;
            white-space: nowrap;
        }

        .complete {
            color: #e95353;
            border: 2px solid #e95353;
            font-weight: 600;
            font-size: 14px;
            padding: 3px 14px;
            position: absolute;
            transform: rotate(-90deg) translateY(-110%) translateX(5%);
            top: 50%;
        }

        .column__price-first {
            font-size: 12px;
            font-weight: 600;
            margin-bottom: 15px;
            color: #11325b;
        }

        .column__price-second {
            font-weight: 300;
            font-size: 12px;
            margin-top: 5px;
            color: #11325b;
            margin-bottom: 12px;
            min-height: 17px;
        }

        &__body {
            height: 100%;
            max-height: 240px;
            background-color: #cbcbcb;
            display: flex;
            align-items: flex-end;
            max-width: 60px;
            width: 100%;
            position: relative;

            .column__bg {
                width: 100%;
            }
        }
    }
}
